.logo-wrapper {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	height: 120px;
	z-index: 10;
}
.logo-img {
	margin-top: 70px;
	width: 100%;
    max-width: 488px;
    min-width: 125px;
}
.home-main {
	width: 100%;
	min-height: 500px;
	background: #ccc;
}
.title-wrapper {
  padding-top: 5rem;
  padding-bottom: 5rem;
  background: #3a3435;
  clear: both;
  position: relative;
  overflow: hidden;
  height: 289px;

  &:before {
    content: "";
    background: rgba(17, 174, 255, 0.2);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
  }
}
.blog-header {
  position: relative;
  z-index: 99;

  &:after {
    content: '';
    height: 3px;
    background: #11aeff;
    display: block;
    width: 45%;
    margin: auto;
    margin-top: 15px;
  }
}
.blog-sign-up {
	width: 100%;
	background: #11aeff;
	min-height: 400px;
}
.affiliates {
	width: 100%;
	min-height: 380px;
	background: #fff;
}
.footer {
	min-height: 780px;
	width: 100%;
	background: #000;
}
